import React, { lazy, Suspense, useState } from 'react';
import { View, Text, StyleSheet, Pressable, Platform, useWindowDimensions } from 'react-native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import { useNavigation } from '@react-navigation/native';
import { RootStackParamList, RootStackNavigationProp } from '../types/navigation';
import { SearchBar } from '../components/SearchBar';
import { LoadingIndicator } from '../components/LoadingIndicator';
import { TableSettings } from '../components/TableSettings';
import { colors } from '../theme/colors';
import { useFoils } from '../hooks/useFoils';
import { useFilteredFoils } from '../hooks/useFilteredFoils';
import { FilterOptions } from '../types/filters';

// Lazy load screens
const BrowseScreen = lazy(() => import('../screens/BrowseScreen'));
const PrivacyPolicy = lazy(() => import('../screens/legal/PrivacyPolicy'));
const TermsOfService = lazy(() => import('../screens/legal/TermsOfService'));
const Contact = lazy(() => import('../screens/legal/Contact'));

const Stack = createNativeStackNavigator<RootStackParamList>();

interface AppNavigatorProps {
  searchQuery: string;
  onSearch: (text: string) => void;
}

const DEFAULT_COLUMNS = [
  { id: 'brand', label: 'Brand', isVisible: true },
  { id: 'series', label: 'Series', isVisible: true },
  { id: 'model', label: 'Model', isVisible: true },
  { id: 'area', label: 'Area', isVisible: true },
  { id: 'wingspan', label: 'Span', isVisible: true },
  { id: 'aspectRatio', label: 'AR', isVisible: true },
  { id: 'type', label: 'Type', isVisible: true },
  { id: 'price', label: 'Price', isVisible: true },
  { id: 'link', label: 'Link', isVisible: true }
];

// Add constants for layout
const CONTAINER_PADDING = 24;
const MAX_CONTENT_WIDTH = 1200;
const MOBILE_BREAKPOINT = 768;

export const AppNavigator = ({ searchQuery, onSearch }: AppNavigatorProps) => {
  const navigation = useNavigation<RootStackNavigationProp>();
  const { foils } = useFoils();
  const [columns, setColumns] = useState(DEFAULT_COLUMNS);
  const [filters, setFilters] = useState<FilterOptions>({
    brands: [],
    series: [],
    type: []
  });
  const { filterOptions } = useFilteredFoils(foils, filters);
  const { width } = useWindowDimensions();
  const isMobile = width < MOBILE_BREAKPOINT;

  const handleLogoPress = () => {
    navigation.reset({
      index: 0,
      routes: [{ name: 'Browse', params: { searchQuery } }],
    });
  };

  const handleFilterChange = (type: keyof FilterOptions, value: string) => {
    setFilters(prev => {
      const newFilters = { ...prev };
      if (type === 'type') {
        const array = prev.type as string[];
        newFilters.type = array.includes(value)
          ? array.filter(item => item !== value)
          : [...array, value];
      } else {
        const array = prev[type] as string[];
        newFilters[type] = array.includes(value)
          ? array.filter(item => item !== value)
          : [...array, value];
      }
      return newFilters;
    });
  };

  const handleColumnVisibilityChange = (columnId: string) => {
    setColumns(prev => prev.map(col => 
      col.id === columnId ? { ...col, isVisible: !col.isVisible } : col
    ));
  };

  const handleClearFilters = () => {
    setFilters({
      brands: [],
      series: [],
      type: []
    });
  };

  const handleResetColumns = () => {
    setColumns(DEFAULT_COLUMNS);
  };

  return (
    <Stack.Navigator
      screenOptions={{
        headerStyle: {
          backgroundColor: colors.background.primary,
        },
        headerShadowVisible: false,
      }}
    >
      <Stack.Screen 
        name="Browse"
        options={{
          headerTitle: '',
          headerLeft: () => (
            <View style={styles.headerOuterContainer}>
              <View style={[
                styles.headerMainContainer,
                !isMobile && styles.headerMainContainerWeb
              ]}>
                <Pressable 
                  onPress={handleLogoPress}
                  style={({ pressed }) => [
                    styles.logoContainer,
                    pressed && styles.logoPressed
                  ]}
                >
                  <Text style={styles.headerTitle}>FoilQuiver</Text>
                </Pressable>
                <View style={styles.searchBarContainer}>
                  <SearchBar 
                    value={searchQuery}
                    onChangeText={onSearch}
                    onClear={() => onSearch('')}
                  />
                </View>
              </View>
            </View>
          ),
          headerRight: () => (
            <View style={[
              styles.headerRightContainer,
              !isMobile && styles.headerRightContainerWeb
            ]}>
              <TableSettings
                filterOptions={filterOptions}
                selectedBrands={filters.brands}
                selectedSeries={filters.series}
                selectedTypes={filters.type}
                onFilterChange={handleFilterChange}
                onClearFilters={handleClearFilters}
                columns={columns}
                onColumnVisibilityChange={handleColumnVisibilityChange}
                onResetColumns={handleResetColumns}
              />
            </View>
          ),
        }}
      >
        {(props) => (
          <Suspense fallback={<LoadingIndicator />}>
            <BrowseScreen 
              {...props} 
              filters={filters}
              onFilterChange={handleFilterChange}
              columns={columns}
            />
          </Suspense>
        )}
      </Stack.Screen>
      <Stack.Screen 
        name="Terms"
        options={{
          title: 'Terms of Service',
          headerStyle: {
            backgroundColor: '#f4f4f4',
          },
        }}
      >
        {(props) => (
          <Suspense fallback={<LoadingIndicator />}>
            <TermsOfService {...props} />
          </Suspense>
        )}
      </Stack.Screen>
      <Stack.Screen 
        name="Privacy"
        options={{
          title: 'Privacy Policy',
          headerStyle: {
            backgroundColor: '#f4f4f4',
          },
        }}
      >
        {(props) => (
          <Suspense fallback={<LoadingIndicator />}>
            <PrivacyPolicy {...props} />
          </Suspense>
        )}
      </Stack.Screen>
      <Stack.Screen 
        name="Contact"
        options={{
          title: 'Contact Us',
          headerStyle: {
            backgroundColor: '#f4f4f4',
          },
        }}
      >
        {(props) => (
          <Suspense fallback={<LoadingIndicator />}>
            <Contact {...props} />
          </Suspense>
        )}
      </Stack.Screen>
    </Stack.Navigator>
  );
};

const styles = StyleSheet.create({
  headerOuterContainer: {
    flex: 1,
    backgroundColor: colors.background.primary,
    paddingHorizontal: CONTAINER_PADDING,
  },
  headerMainContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    gap: 16,
    width: '100%',
    height: 56,
  },
  headerMainContainerWeb: {
    maxWidth: MAX_CONTENT_WIDTH,
    marginHorizontal: 'auto',
  },
  logoContainer: {
    height: '100%',
    justifyContent: 'center',
    minWidth: 120,
  },
  logoPressed: {
    opacity: 0.7,
  },
  headerTitle: {
    color: colors.primary,
    fontSize: 24,
    fontWeight: 'bold',
  },
  searchBarContainer: {
    flex: 1,
    height: 40,
    alignSelf: 'center',
    marginLeft: 16,
    marginRight: 48,
  },
  headerRightContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    height: '100%',
    paddingRight: CONTAINER_PADDING,
  },
  headerRightContainerWeb: {
    position: 'absolute',
    right: 0,
    top: '50%',
    transform: [{ translateY: -20 }],
  },
}); 