import { useMemo } from 'react';
import { BaseProduct } from '../types/scraper';
import { FilterOptions } from '../types/filters';

export function useFilteredFoils(foils: BaseProduct[], filters: FilterOptions) {
  const filterOptions = useMemo(() => {
    const brands = Array.from(new Set(foils.map(foil => foil.brand))).sort();
    const series = Array.from(new Set(foils.map(foil => foil.series))).sort();
    const types = ['front', 'tail'];

    return { brands, series, types };
  }, [foils]);

  const filteredFoils = useMemo(() => {
    return foils.filter(foil => {
      const brandMatch = filters.brands.length === 0 || filters.brands.includes(foil.brand);
      const seriesMatch = filters.series.length === 0 || filters.series.includes(foil.series);
      const typeMatch = filters.type.length === 0 || filters.type.includes(foil.type);

      return brandMatch && seriesMatch && typeMatch;
    });
  }, [foils, filters.brands, filters.series, filters.type]);

  return {
    filteredFoils,
    filterOptions,
  };
} 